import React, { useState } from 'react';
import { Button, Modal, Form, Input } from 'antd';

function PasswordModal({ visible, onCancel }) {
    const [form] = Form.useForm();
    const [loading, setLoading] = useState(false);
    const DEFAULT_PASSWD = '1541'

    const loginSuccess = () => {
        localStorage.setItem('isLoggedIn', 'true');
        window.location.href = '/';
    }

    const handleSubmit = () => {
        form.validateFields()
            .then(res => {
                setLoading(true);
                if (res.password === DEFAULT_PASSWD) {
                    //  
                    loginSuccess();
                } else {
                    alert('  .')
                }
                setTimeout(() => {
                    setLoading(false);
                    onCancel();
                }, 1000);
            })
            .catch(error => {
                console.error(error);
            });
    };

    return (
        <Modal
            title=" "
            visible={visible}
            onCancel={onCancel}
            footer={[
                <Button key="cancel" onClick={onCancel}></Button>,
                <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}></Button>
            ]}
        >
            <Form form={form} layout="vertical">
                <Form.Item
                    name="password"
                    label="  (   )"
                    rules={[
                        { required: true, message: ' ' }
                    ]}
                >
                    <Input.Password />
                </Form.Item>
            </Form>
        </Modal>
    );
}

export default PasswordModal;