import React, { useState, useEffect, useRef } from 'react';
import Axios from 'axios';
import { Button, List } from 'antd';
import groundJpg from '../../../images/ground.png';
import profile1 from '../../../images/profile/1.jpg';
import profile2 from '../../../images/profile/2.jpg';
import profile3 from '../../../images/profile/3.jpg';
import captureAndShare from "../../adminPage/ShareResult";

function DailyTeamPage() {
    const all = [
        { id: 0, name: "" }, { id: 1, name: "" }, { id: 2, name: "" }, { id: 3, name: "" }, { id: 4, name: "" },
        { id: 5, name: "" }, { id: 6, name: "" }, { id: 7, name: "" }, { id: 8, name: "" }, { id: 9, name: "" },
        { id: 10, name: "" }, { id: 11, name: "" }, { id: 12, name: "" }, { id: 13, name: "" },
        { id: 14, name: "" }, { id: 15, name: "" }, { id: 16, name: "" }, { id: 17, name: "" }, { id: 18, name: "" },
        { id: 19, name: "" }, { id: 20, name: "" }, { id: 21, name: "" }, { id: 22, name: "" }, { id: 23, name: "" }, 
        { id: 24, name: "" }, { id: 25, name: "" }, { id: 26, name: "" }, { id: 27, name: "" }, { id: 28, name: "" },
        { id: 29, name: "" }, { id: 30, name: "" }, { id: 31, name: "" }, { id: 32, name: "" }, { id: 33, name: "" },
        { id: 34, name: "" }, { id: 35, name: "" }, { id: 36, name: "" },
        { id: 37, name: "" }, { id: 38, name: "" }, { id: 39, name: "" }, { id: 40, name: "" }
    ];

    const [aTeam, setATeam] = useState([]);
    const [bTeam, setBTeam] = useState([]);
    const [cTeam, setCTeam] = useState([]);
    
    const [aResult, setAResult] = useState([]);
    const [bResult, setBResult] = useState([]);
    const [cResult, setCResult] = useState([]);
    const [aIds, setAIds] = useState([]);
    const [bIds, setBIds] = useState([]);
    const [cIds, setCIds] = useState([]);

    useEffect(() => { 
        Axios.get('/api/record/getDailyTeam')
        .then(response => {
            console.log(response.data)
            if(response.data.success) {
                const { A, B, C, Result } = response.data.dailyTeam;
                setAIds(A);
                setBIds(B);
                setCIds(C);
                setAResult(Result.A);
                setBResult(Result.B);
                setCResult(Result.C);
                const mapMembersWithProfile = (ids, profileImage) => 
                    all.filter(member => ids.includes(member.id))
                        .map(member => ({ ...member, image: profileImage }));

                setATeam(mapMembersWithProfile(A, profile1)); 
                setBTeam(mapMembersWithProfile(B, profile2)); 
                setCTeam(mapMembersWithProfile(C, profile3)); 
            } else {
                alert('    .')
            }
        })  
    }, [])

    const plusWinByIds = (id) => {
        Axios.post('/api/record/plusWin', { id })
            .then(response => {
                if(response.data.success) {
                    window.location.reload();
                } else {
                    alert('  .')
                }
            })
    };

    const plusDrawByIds = (id) => {
        Axios.post('/api/record/plusDraw', { id })
            .then(response => {
                if(response.data.success) {
                    window.location.reload();
                } else {
                    alert('  .')
                }
            })
    };

    const plusLoseByIds = (id) => {
        Axios.post('/api/record/plusLose', { id })
            .then(response => {
                if(response.data.success) {
                    window.location.reload();
                } else {
                    alert('  .')
                }
            })
    };

    const plusWinByTeam = (team) => {
        Axios.post('/api/record/winTeam', { team })
            .then(response => {
                if(response.data.success) {
                    //window.location.reload();
                } else {
                    alert('   .')
                }
            })
    };

    const plusDrawByTeam = (team) => {
        Axios.post('/api/record/drawTeam', { team })
            .then(response => {
                if(response.data.success) {
                    //window.location.reload();
                } else {
                    alert('   .')
                }
            })
    };

    const plusLoseByTeam = (team) => {
        Axios.post('/api/record/loseTeam', { team })
            .then(response => {
                if(response.data.success) {
                    //window.location.reload();
                } else {
                    alert('   .')
                }
            })
    };  

    let date = new Date();
    let resultRef = useRef(null);

    return (
        <div style={{ textAlign: 'center', minHeight: "100vh", color: 'yellow' }}>
            <div ref={resultRef} style={{ padding: '20px', background: `url(${groundJpg})`, backgroundSize: 'cover' }}>
                <div style={{ padding: "10px", color: 'white' }}>
                    <h1 style={{ fontSize: '27px', marginTop: '37px'}}>    </h1>
                    <p>{date.toLocaleDateString()}</p>
                    <p>       .</p>
                    <p> :3 / : 1 / : 0</p>
                </div>

                {/* A */}
                <div style={{ marginBottom: '20px', border: '2px solid red', padding: '10px', borderRadius: '8px', position: 'relative', overflow: 'hidden', minHeight: '330px' }}>
                    <h2 style={{ color: 'white', textAlign: 'center' }}>A</h2>
                    <List
                        grid={{ gutter: 10, column: 5 }}
                        dataSource={aTeam}
                        renderItem={(member) => (
                            <List.Item>
                                <div style={{ textAlign: 'center' }}>
                                    <img src={member.image} alt={member.name} style={{ width: '50px', height: '50px', borderRadius: '50%' }} />
                                    <p style={{ color: 'white' }}>{member.name}</p>
                                </div>
                            </List.Item>
                        )}
                    />
                    <div
                        style={{
                            position: 'absolute',
                            bottom: '2%',
                            right: '4%',
                            backgroundColor: 'rgb(42, 133, 251)',
                            padding: '10px',
                            borderRadius: '8px',
                            color: 'white',
                            textAlign: 'center',
                            fontSize: '14px',
                            width: '60px'
                        }}
                    >
                        <p style={{ margin: '5px 0' }}>: <strong>{aResult.win}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{aResult.draw}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{aResult.lose}</strong></p>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', gap: '10px', marginTop: '10px', position: 'absolute', bottom: '2%', left: '4%' }}>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('A  ?')) {
                                    plusWinByTeam('A');
                                    plusWinByIds(aIds);
                                }
                            }} 
                            style={{ background: '#28a745', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('A  ?')) {
                                    plusDrawByTeam('A');
                                    plusDrawByIds(aIds);
                                }
                            }}
                            style={{ background: '#6c757d', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('A  ?')) {
                                    plusLoseByTeam('A');
                                    plusLoseByIds(aIds);
                                }
                            }}
                            style={{ background: '#dc3545', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                    </div>
                </div>

                {/* B */}
                <div style={{ marginBottom: '20px', border: '2px solid blue', padding: '10px', borderRadius: '8px', position: 'relative', overflow: 'hidden', minHeight: '330px' }}>
                    <h2 style={{ color: 'white', textAlign: 'center' }}>B</h2>
                    <List
                        grid={{ gutter: 10, column: 5 }}
                        dataSource={bTeam}
                        renderItem={(member) => (
                            <List.Item>
                                <div style={{ textAlign: 'center' }}>
                                    <img src={member.image} alt={member.name} style={{ width: '50px', height: '50px', borderRadius: '50%' }} />
                                    <p style={{ color: 'white' }}>{member.name}</p>
                                </div>
                            </List.Item>
                        )}
                    />
                    <div
                        style={{
                            position: 'absolute',
                            bottom: '2%',
                            right: '4%',
                            backgroundColor: 'rgb(42, 133, 251)',
                            padding: '10px',
                            borderRadius: '8px',
                            color: 'white',
                            textAlign: 'center',
                            fontSize: '14px',
                            width: '60px'
                        }}
                    >
                        <p style={{ margin: '5px 0' }}>: <strong>{bResult.win}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{bResult.draw}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{bResult.lose}</strong></p>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', gap: '10px', marginTop: '10px', position: 'absolute', bottom: '2%', left: '4%' }}>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('B  ?')) {
                                    plusWinByTeam('B');
                                    plusWinByIds(bIds);
                                }
                            }} 
                            style={{ background: '#28a745', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('B  ?')) {
                                    plusDrawByTeam('B');
                                    plusDrawByIds(bIds);
                                }
                            }}
                            style={{ background: '#6c757d', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('B  ?')) {
                                    plusLoseByTeam('B');
                                    plusLoseByIds(bIds);
                                }
                            }}
                            style={{ background: '#dc3545', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                    </div>
                </div>

                {/* C */}
                <div style={{ marginBottom: '20px', border: '2px solid white', padding: '10px', borderRadius: '8px', position: 'relative', overflow: 'hidden', minHeight: '330px' }}>
                    <h2 style={{ color: 'white', textAlign: 'center' }}>C</h2>
                    <List
                        grid={{ gutter: 10, column: 5 }}
                        dataSource={cTeam}
                        renderItem={(member) => (
                            <List.Item>
                                <div style={{ textAlign: 'center' }}>
                                    <img src={member.image} alt={member.name} style={{ width: '50px', height: '50px', borderRadius: '50%' }} />
                                    <p style={{ color: 'white' }}>{member.name}</p>
                                </div>
                            </List.Item>
                        )}
                    />
                    <div
                        style={{
                            position: 'absolute',
                            bottom: '2%',
                            right: '4%',
                            backgroundColor: 'rgb(42, 133, 251)',
                            padding: '10px',
                            borderRadius: '8px',
                            color: 'white',
                            textAlign: 'center',
                            fontSize: '14px',
                            width: '60px'
                        }}
                    >
                        <p style={{ margin: '5px 0' }}>: <strong>{cResult.win}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{cResult.draw}</strong></p>
                        <p style={{ margin: '5px 0' }}>: <strong>{cResult.lose}</strong></p>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center', gap: '10px', marginTop: '10px', position: 'absolute', bottom: '2%', left: '4%' }}>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('C  ?')) {
                                    plusWinByTeam('C');
                                    plusWinByIds(cIds);
                                }
                            }} 
                            style={{ background: '#28a745', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('C  ?')) {
                                    plusDrawByTeam('C');
                                    plusDrawByIds(cIds);
                                }
                            }}
                            style={{ background: '#6c757d', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                        <Button 
                            type="primary"
                            onClick={() => {
                                if (window.confirm('C  ?')) {
                                    plusLoseByTeam('C');
                                    plusLoseByIds(cIds);
                                }
                            }}
                            style={{ background: '#dc3545', width: '65px', height: '40px', borderRadius: '6px', fontSize: '13px', marginTop: '10px' }}>
                        </Button>
                    </div>
                </div>
                <div style={{ display: "flex", justifyContent: "flex-end" }}>
                    <Button type="primary" onClick={() => captureAndShare(resultRef)} style={{ background: '#30d946', width: '145px', height: '45px', borderRadius: '6px', fontSize: '13px', marginTop: '10px', color: 'black', fontWeight: 'bolder' }}></Button>
                </div>
            </div>
        </div>
    );
}

export default DailyTeamPage;
